body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}


.header-container {
    margin-top: 50px;
    /* width: 100%; */
}
.nav-container{
    width: 100%;
	height: 80px;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: flex-end;
	        justify-content: flex-end;
	-webkit-flex-flow: row;
	        flex-flow: row;
	-webkit-align-items: center;
	        align-items: center;
	background-color: #ffffff;
	position: fixed;
    top: 0;
    z-index: 1000;

}
.btn{
    margin-left: 1.5rem;
    border: transparent;
    color:rgb(172, 170, 170);
    border-radius: 10px;
    padding: 10px;
    font-size: 15px;
    font-weight: bold;
    background: white;
    /* margin: 20px; */
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
}
.btn:hover { 
    background-color: #ddddddb9;
  }

#search-input{
    background-color: #ddddddb9;
    border-color: #d6d2d2;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    /* background-color: transparent; */
    border: 0;
    border-radius: 4px;
    box-shadow: none;
    box-sizing: border-box;
    color: rgb(172, 170, 170);
    font-size: 16px;
    font-weight: 600;
    /* height: 100%; */
    line-height: 20px;
    outline: none;
    padding: 10px 40px 10px 52px;
    /* margin-top: 20px; */
    width: 58%;
}
#iconPinteres{
    font-size: 35px;
    color: #e60023;
    margin-top: 1rem;
    margin-right: 1rem;
    margin-bottom: 1rem;
    /* margin: 20px; */
}
#icon{
    font-size: 30px;
    color: rgb(172, 170, 170);
    margin-left: 1.5rem;
    margin-right: 1.5rem;
}

#icon-user{
    font-size: 15px;
    margin: 0 5px;
}


.cards{
    -webkit-column-count: 5;
            column-count: 5;
    /* column-gap: 2px; */
    /* padding: 10px;
    margin: 20px 15px 10px 15px; */
    display: inline-block;
    padding-top: 30px;


}
img {
    border-radius: 12px;
    margin-bottom: 5px;
}

.container-modal {
    width: 100%;
    height: 100%;
    position: fixed;
    top: 20px;
    left: 0;
    z-index: 100;
    background: -webkit-linear-gradient(rgb(238, 238, 238) 300px, rgb(255, 255, 255) 100%);
    background: linear-gradient(rgb(238, 238, 238) 300px, rgb(255, 255, 255) 100%);
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
}

#modal-cards {
    width: 300px;
    height: 400px;
    display: -webkit-flex;
    display: flex;
    float: left;
    margin-left: 10%;
}

.main-modal {
    margin-top: 2rem;
    width: 60%;
    height: 70%;
    background-color: white;
    margin-top: 10%;
    left: 0;
    border-radius: 8px;
}

.header-modal {
    width: 100%;
    height: 80px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
}

.btn-modal {
    border: transparent;
    color: #333;
    border-radius: 10px;
    font-weight: bold;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    margin: 20px;
    font-size: 15px;
    text-align: center;
    background: #ddddddb9;
}

.btn-modal:hover {
    background-color: #ddddddb9;
}

.save {
    background-color: #e60023;
    color: white;
}

#icon-ellipsis {
    font-size: 20px;
    color: rgb(172, 170, 170);
    margin-left: 1.5rem;
    margin-top: 30px;
    display: block;
    float: left;
    margin: auto;
    margin-left: 25px;
}

#icon-upload {
    font-size: 15px;
    margin: 0 5px;
}

#icon-angle {
    cursor: pointer;
    font-size: 35px;
    margin: 0 5px;
    color: #333;
    padding-top: 10px;
    margin: 150px 100px auto 0;
}
